import React from 'react';
import './about.css';
import Navbar from 'src/components/Navbar/Navbar';
import Header from './Header/Header';
import Content from './Content/Content';
import ContactFooter from 'src/components/ContactFooter/ContactFooter';
import Stripe from 'src/components/Stripe/Stripe';

const About = () => {
    return (
        <div className="about">
            <Navbar black />

            <Header />

            <Content />

            <div className="works-with">
                <div className="title">Posso te ajudar com</div>
                <div className="row">
                    <div className="section">
                        <div className="number">01</div>
                        <Stripe height='2' />
                        <div className="titleSkill">
                            Design
                        </div>
                        <div className="skillText">
                            Com um slido histrico na criao de sites e aplicativos, ofereo designs digitais fortes e fceis de usar. A marca slida da empresa  a base de qualquer site bem-sucedido.                        
                        </div>
                    </div>
                    <div className="section">
                        <div className="number">02</div>
                        <Stripe height='2' />
                        <div className="titleSkill">
                            Desenvolvimento
                        </div>
                        <div className="skillText">
                            Eu construo sites escalveis a partir do zero e criao de novas features em sites pr-existentes. Meu foco  o front-end contendo desenvolvimento de contedo, micro animaes, transies e interao.
                        </div>
                    </div>
                    <div className="section">
                        <div className="number">03</div>
                        <Stripe height='2' />
                        <div className="titleSkill">
                            O pacote completo
                        </div>
                        <div className="skillText">
                            Um site completo desde o conceito at a implementao. Com um grande senso de design e minha habilidade de desenvolvimento, consigo criar projetos que se encaixam perfeitamente no contexto da sua necessidade.
                        </div>
                    </div>
                </div>
            </div>

            <ContactFooter />
        </div>
    );
}

export default About;